import React, { Component } from 'react';
import './index.css'
import axios from 'axios'

class Imprints extends Component {
    // Adds a class constructor that assigns the initial state values:
    constructor () {
        super();
        this.state = {
            catelog: {}
        };
    }
    // This is called when an instance of a component is being created and inserted into the DOM.
    componentWillMount () {
        this.getMyReposWS()
    }

    refreshBtnClicked(e) {
        e.preventDefault(e);
        this.getMyReposWS()
    }
    getMyReposWS() {
        if (!navigator.onLine) {
            this.setState({ catelog: JSON.parse(localStorage.getItem('catelog'))});
            return
        }  else {
            axios.get('http://localhost:3000/Resources/catalog.json')
            .then(response => {
                // We set the repos array in the state.
                localStorage.setItem("catelog", JSON.stringify(response.data));
                this.setState({ repos: response.data });
            })
            // Catch any error here
            .catch(error => {
                console.log(error)
            })
        }
        
    }
    renderRepos(repo) {
        return (
            <li key={repo.id} className="card">
                <div className="container">
                    <h4><b>{repo.name}</b></h4> 
                    <p>{repo.description}</p> 
                </div>
            </li>
        );
    }

    // The render method contains the JSX code which will be compiled to HTML.
    render() {
        return (
            <div className="view">
            <h1>About</h1>
            <div className="content">
                <h2>Coming soon...</h2>
              <div className="content--inner">
                
              </div>
            </div>
          </div>
    
        )
    }
}

export default Imprints;
