import React, { Component } from 'react';
import './index.css'
import axios from 'axios'




class Repos extends Component {
    // Adds a class constructor that assigns the initial state values:
    constructor () {
        super();
        this.state = {
            repos: []
        };
    }
    // This is called when an instance of a component is being created and inserted into the DOM.
    componentWillMount () {
        this.getMyReposWS()
    }

    refreshBtnClicked(e) {
        e.preventDefault(e);
        this.getMyReposWS()
    }
    getMyReposWS() {
        if (!navigator.onLine) {
            this.setState({ repos: JSON.parse(localStorage.getItem('repos'))});
            return
        }  else {
            axios.get('https://api.github.com/users/vigneshuvi/repos')
            .then(response => {
                // We set the repos array in the state.
                localStorage.setItem("repos", JSON.stringify(response.data));
                this.setState({ repos: response.data });
            })
            // Catch any error here
            .catch(error => {
                console.log(error)
            })
        }
        
    }
    renderRepos(repo) {
        return (
            <li key={repo.id} className="card">
                <div className="container">
                    <h4><b>{repo.name}</b></h4> 
                    <p>{repo.description}</p> 
                </div>
            </li>
        );
    }

    // The render method contains the JSX code which will be compiled to HTML.
    render() {
        return (
            <div className="view">
                <h1>Repositories</h1>
                <div className="content">
                    <div className="content--inner">
                        <ul>
                        {
                            this.state.repos.map ( (repo) => {
                                return this.renderRepos(repo)
                            })
                        }
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}

export default Repos;
